import React, { useEffect, useState } from "react";
import { Carousel } from "antd";
import { useStoreState, useStoreActions } from "easy-peasy";
import { Skeleton } from "@material-ui/lab";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";

const MyCarousel = (props) => {
  const { Featured, loading } = useStoreState((state) => state.vox);
  const { getPosts } = useStoreActions((state) => state.vox);
  const width = window.innerWidth;
  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: "center",
      color: theme.palette.text.secondary,
    },
  }));
  useEffect(() => {
    getPosts();
  }, []);
  const [classval, setClassval] = useState("");
  function container() {
    if (width > 1300) {
      setClassval("container");
    } else {
      setClassval("");
    }
  }
  const classes = useStyles();

  useEffect(() => {
    container();
  });
  return (
    <Grid container>
      {/* {loading ? (
        <Skeleton variant="rect" animation="wave" width={1265} height={465} />
      ) : ( */}
      <Grid item>
        {/* className={classval} */}

        <Carousel fade dotPosition="left" autoplay>
          {Featured.slice(0, 3).map((post) => (
            <div className="car" key={post.id}>
              <img
                className="car-image"
                src={post.imageUrl}
                alt={post.title.toString()}
              />
              <p className="car-text heading">{post.title}</p>
              {/* <p className="car-text sub-heading" style={{  }}>
            {post.description}
          </p> */}
            </div>
          ))}
        </Carousel>
      </Grid>
      {/* )} */}
    </Grid>
  );
};
export default MyCarousel;
