import React, { useState, useEffect } from "react";
import { Table, Space, Modal, Button, Tag } from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import Error from "./Error";
import { useStoreState, useStoreActions } from "easy-peasy";
import AddIcon from "@material-ui/icons/Add";
import BallotIcon from "@material-ui/icons/Ballot";
import DeleteTwoToneIcon from "@material-ui/icons/DeleteTwoTone";
import { Link } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import EditTwoToneIcon from "@material-ui/icons/EditTwoTone";
const { Column } = Table;
const { confirm } = Modal;
const Posts = () => {
  // const useStyles = makeStyles((theme) => ({
  //   fab: {
  //     position: "absolute",
  //     bottom: theme.spacing(2),
  //     right: theme.spacing(2),
  //   },
  // }));

  const [classval, setClassval] = useState("");
  const [open, setOpen] = useState(false);
  const [err, setErr] = useState(false);
  const width = window.innerWidth;
  const [createMode, setSreateMode] = useState(false);
  const handleClickOpen = () => {
    setOpen(true);
  };

  const dnone = () => {
    if (width <= 420) {
      setClassval("d-none");
    } else {
      setClassval("");
    }
  };

  const { loading, posts } = useStoreState((state) => state.vox);
  const { getPosts, performPostDelete } = useStoreActions(
    (actions) => actions.vox
  );

  useEffect(() => {

    dnone();
    // eslint-disable-next-line
  }, []);

  const com = () => {
    if (!err) {
      return (
        <div>
          <h4 className="h">
            <BallotIcon fontSize="large" />
            Posts
          </h4>
          <hr />
          <Link to="/add">
            <Button
              onClick={handleClickOpen}
              style={{ marginBottom: "12px" }}
              type="primary"
              shape="round"
              icon={<AddIcon fontSize="small" />}
              size="medium"
            >
              Create
            </Button>
          </Link>
          {/* loading={loading} */}
          <Table dataSource={posts} loading={loading} size="small">
            <Column className={classval} title="ID" dataIndex="id" key="id" />
            <Column title="Title" dataIndex="title" key="title" />
            <Column
              className={classval}
              title="Category"
              dataIndex="genre"
              key="category"
            />
            <Column
              className={classval}
              title="Type"
              dataIndex="type"
              key="type"
            />
            <Column
              className={classval}
              title="Tags"
              dataIndex="tags"
              key="tags"
              render={(tags) => (
                <>
                  {tags.map((tag) => (
                    <Tag color="blue" key={tag}>
                      {tag.name}
                    </Tag>
                  ))}
                </>
              )}
            />
            <Column
              title="Action"
              dataIndex="id"
              key="action"
              render={(key, id) => (
                // eslint-disable-next-line
                <Space size="middle">
                  {/* eslint-disable-next-line */}
                  <Button
                    size="middle"
                    type="text"
                    key={key}
                    icon={
                      <EditTwoToneIcon
                        style={{ color: "#1890ff" }}
                        fontSize="small"
                      />
                    }
                  ></Button>
                  {/* eslint-disable-next-line */}
                  <a
                    onClick={() => {
                      confirm({
                        title: "Are you about deleting this Post?",
                        icon: <ExclamationCircleOutlined />,
                        content: "This Action can't be undone",
                        okText: "Yes",
                        okType: "danger",
                        cancelText: "No",
                        onOk() {
                          performPostDelete(id.id);
                          // console.log(id.id);
                        },
                        onCancel() {
                          console.log("Cancel");
                        },
                      });
                    }}
                  >
                    <Button
                      size="middle"
                      type="text"
                      icon={
                        <DeleteTwoToneIcon
                          style={{ color: "red" }}
                          fontSize="small"
                        />
                      }
                      // type="danger"
                      key={key}
                    ></Button>
                    {/* eslint-disable-next-line */}
                  </a>
                </Space>
              )}
            />
          </Table>
        </div>
      );
    } else {
      return <Error />;
    }
  };
  const comp = com();
  return <div>{comp}</div>;
};

export default Posts;
