import { StoreProvider } from "easy-peasy";
import Admin from "./components/private/Admin";
import { store } from "./store/store";
import {
  BrowserRouter as Router,
  HashRouter,
  Route,
  Switch,
  // withRouter,
  // useLocation,
} from "react-router-dom";
import Posts from "./components/public/Post";
import PageNotFound from "./components/common/Error";
import { Homepage } from "./components/public/Homepage";
import MyEditor from "./components/private/MyEditor";
function App() {
  return (
    <StoreProvider store={store}>
      <HashRouter>
        <Switch>
          <Route path="/" exact component={Homepage} />
          <Route exact path="/add" component={MyEditor} />
          <Route path="/admin" exact component={Admin} />
          <Route path="/" component={PageNotFound} />
        </Switch>
      </HashRouter>
    </StoreProvider>
  );
}

export default App;
