import React, { useState, useEffect } from "react";
import { Button } from "antd";
import { Divider } from "@material-ui/core";
import MenuItem from "@material-ui/core/MenuItem";
import TextField from "@material-ui/core/TextField";
import AppBar from "@material-ui/core/AppBar";
import Typography from "@material-ui/core/Typography";
import Toolbar from "@material-ui/core/Toolbar";
import { makeStyles } from "@material-ui/core/styles";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { useStoreState, useStoreActions } from "easy-peasy";

import { Link } from "react-router-dom";

const MyEditor = () => {
  const { categories, loading } = useStoreState((state) => state.vox);
  const { performPostAdd, getCategories } = useStoreActions(
    (state) => state.vox
  );
  const types = [
    { id: "1", name: "News" },
    { id: "2", name: "Latest" },
    { id: "3", name: "Featured" },
    { id: "4", name: "Trending" },
  ];
  const [post, setPost] = useState({
    title: "",
    description: "",
    content: "",
    categoryId: "",
    type: "",
    imageUrl: "",
    thumbnailImageUrl: "",
  });
  const [open, setOpen] = useState(false);
  const [show, setShow] = useState(false);
  const submitPost = () => {
    setOpen(false);
    setPost({
      title: "",
      description: "",
      content: "",
      categoryId: "",
      type: "",
      imageUrl: "",
      thumbnailImageUrl: "",
    });
    performPostAdd(post);
  };
  const handleClose = () => {
    setOpen(false);
  };

  useEffect(() => {
    getCategories();
    // eslint-disable-next-line
  }, []);
  const useStyles = makeStyles((theme) => ({
    appBar: {
      position: "relative",
    },
    title: {
      marginLeft: theme.spacing(2),
      flex: 1,
      color: "white",
    },
  }));
  const classes = useStyles();
  const renderPreview = () => {
    if (show) {
      return (
        <div>
          <h6 className="text-dark">Preview</h6>
          <div className="col preview">
            {/* <p>{parse(post.content)}</p> */}
          </div>
        </div>
      );
    }
  };
  const handlePreview = () => {
    show === false ? setShow(true) : setShow(false);
  };
  const preview = renderPreview();
  return (
    <div>
      <AppBar className={classes.appBar}>
        <Toolbar variant="dense">
          <Typography variant="h6" className={classes.title}>
            Add New Blog Post
          </Typography>
        </Toolbar>
      </AppBar>
      <Divider />
      <div className="col">
        <div className="col editor">
          <TextField
            autoFocus
            margin="dense"
            label="Title"
            name="title"
            onChange={(e) => setPost({ ...post, title: e.target.value })}
          />
          <TextField
            autoFocus
            margin="dense"
            label="Description"
            name="description"
            onChange={(e) => setPost({ ...post, description: e.target.value })}
            value={post.description}
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            label="Main Image Link 1265px width 465px height"
            name="imageUrl"
            onChange={(e) => setPost({ ...post, imageUrl: e.target.value })}
            value={post.imageUrl}
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            label="Thumbnail Image Link 825px width 500px height"
            name="imageUrl"
            onChange={(e) =>
              setPost({ ...post, thumbnailImageUrl: e.target.value })
            }
            value={post.thumbnailImageUrl}
            fullWidth
          />
          Body
          <CKEditor
            editor={ClassicEditor}
            data={post.content}
            onChange={(event, editor) => {
              const data = editor.getData();
              setPost({ ...post, content: data });
            }}
          />
          <TextField
            name="category"
            select
            value={post.categoryId}
            label="Category"
            className="mr-5"
            helperText="Select post category"
            onChange={(e) => setPost({ ...post, categoryId: e.target.value })}
          >
            {categories.map((option) => (
              <MenuItem key={option.id} value={option.id}>
                {option.name}
              </MenuItem>
            ))}
          </TextField>
          <TextField
            name="type"
            value={post.type}
            onChange={(e) => setPost({ ...post, type: e.target.value })}
            select
            label="Type"
            helperText="Select post type"
          >
            {types.map((option) => (
              <MenuItem key={option.id} value={option.name}>
                {option.name}
              </MenuItem>
            ))}
          </TextField>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "flex-end",
              marginTop: "30%",
            }}
            className="editor-buttons"
          >
            <Link to="/admin">
              <Button color="primary">Cancel</Button>
            </Link>
            <Button onClick={handlePreview} color="primary">
              Preview
            </Button>
            <Link to="/admin">
              <Button onClick={submitPost} type="primary" color="secondary">
                Publish
              </Button>
            </Link>
          </div>
        </div>
      </div>
      {preview}
    </div>
  );
};

export default MyEditor;
