import { action, thunk, computed } from "easy-peasy";
import {
  categories,
  posts,
  subscribers,
  visitlogs,
  logs,
  adminPost,
  comment,
  reply,
} from "../urls";
import axios from "axios";
let comuptedcomments;

export const voxStore = {
  loading: false,
  error: "",
  categories: [],
  posts: [],
  visitlogs: [],
  logs: [],
  subscribers: [],
  post: {},
  comments: comuptedcomments,
  text:"",

  setLoading: action((state, loading) => {
    state.loading = loading;
  }),
  News: computed((state) =>
    state.posts?.filter(function (el) {
      return el.type === "News";
    })
  ),
  Featured: computed((state) =>
    state.posts?.filter(function (el) {
      return el.type === "Featured";
    })
  ),
  Latest: computed((state) =>
    state.posts?.filter(function (el) {
      return el.type === "Latest";
    })
  ),
  Trending: computed((state) =>
    state.posts?.filter(function (el) {
      return el.type === "Trending";
    })
  ),
  Music: computed((state) =>
    state.posts?.filter(function (el) {
      return el.genre === "Music";
    })
  ),
  Technology: computed((state) =>
    state.posts?.filter(function (el) {
      return el.genre === "Technology";
    })
  ),
  Entertainment: computed((state) =>
    state.posts?.filter(function (el) {
      return el.genre === "Entertainment";
    })
  ),
  Events: computed((state) =>
    state.posts?.filter(function (el) {
      return el.genre === "Events";
    })
  ),
  Health: computed((state) =>
    state.posts?.filter(function (el) {
      return el.genre === "Health";
    })
  ),
  Facts: computed((state) =>
    state.posts?.filter(function (el) {
      return el.genre === "Facts";
    })
  ),

  mostViewed: computed((state) =>
    state.posts
      .sort((a, b) => parseFloat(b.views) - parseFloat(a.views))
      .slice(0, 5)
  ),
  mostLiked: computed((state) =>
    state.posts
      .sort((a, b) => parseFloat(b.likes) - parseFloat(a.likes))
      .slice(0, 5)
  ),
  mostCommented: computed((state) =>
    state.posts
      .sort((a, b) => parseFloat(b.totalComments) - parseFloat(a.totalComments))
      .slice(0, 5)
  ),

  N: computed((state) => state.News.length),
  F: computed((state) => state.Featured.length),
  T: computed((state) => state.Trending.length),
  L: computed((state) => state.Latest.length),

  m: computed((state) => state.Music.length),
  e1: computed((state) => state.Events.length),
  e2: computed((state) => state.Entertainment.length),
  h: computed((state) => state.Health.length),
  t: computed((state) => state.Technology.length),
  f: computed((state) => state.Facts.length),
  comuptedcomments: computed((state) => state.post.comments),

  totalLikesCount: computed((state) =>
    state.posts
      ?.map((item) => item.likes)
      .reduce((prev, next) => prev + next, 0)
  ),
  totalViewsCount: computed((state) =>
    state.posts
      ?.map((item) => item.views)
      .reduce((prev, next) => prev + next, 0)
  ),
  totalCommentsCount: computed((state) =>
    state.posts
      ?.map((item) => item.totalComments)
      .reduce((prev, next) => prev + next, 0)
  ),

  setError: action((state, error) => {
    state.error = error;
  }),
  setCategories: action((state, data) => {
    state.categories = data;
  }),
  getCategories: thunk(async (actions) => {
    actions.setLoading(true);
    try {
      const response = await fetch(categories);
      const json = await response.json();
      if (response.ok) {
        actions.setCategories(json);
      } else {
        throw new Error(json.message);
      }
    } catch (error) {
      actions.setError(error.message);
      actions.setCategories([]);
    }
    actions.setLoading(false);
  }),
  setPosts: action((state, data) => {
    state.posts = data;
  }),
  getPosts: thunk(async (actions) => {
    actions.setLoading(true);
    try {
      const response = await fetch(posts);
      const json = await response.json();
      if (response.ok) {
        actions.setPosts(json);
      } else {
        throw new Error(json.message);
      }
    } catch (error) {
      actions.setError(error.message);
      actions.setPosts([]);
    }
    actions.setLoading(false);
  }),
  setPost: action((state, post) => {
    state.post = post;
  }),
  getPost: thunk((actions, id) => {
    axios
      .get(posts + `/${id}`)
      .then((res) => {
        console.log(res);
        actions.setPost(res.data);
      })
      .catch((err) => {
        console.error(err);
      });
  }),
  setSubscribers: action((state, data) => {
    state.subscribers = data;
  }),
  getSubscribers: thunk(async (actions) => {
    actions.setLoading(true);
    try {
      const response = await fetch(subscribers);
      const json = await response.json();
      if (response.ok) {
        actions.setSubscribers(json);
      } else {
        throw new Error(json.message);
      }
    } catch (error) {
      actions.setError(error.message);
      actions.setSubscribers([]);
    }
    actions.setLoading(false);
  }),
  setVisitlogs: action((state, data) => {
    state.visitlogs = data;
  }),
  getVisitlogs: thunk(async (actions) => {
    action.setLoading = true;
    try {
      const response = await fetch(visitlogs);
      const json = await response.json();
      if (response.ok) {
        actions.setVisitlogs(json);
      } else {
        throw new Error(json.message);
      }
    } catch (error) {
      actions.setError(error.message);
      actions.setVisitlogs([]);
    }
  }),
  setLogs: action((state, data) => {
    state.logs = data;
  }),
  getLogs: thunk(async (actions) => {
    action.loading = true;
    try {
      const response = await fetch(logs);
      const json = await response.json();
      if (response.ok) {
        actions.setLogs(json);
      } else {
        throw new Error(json.response);
      }
    } catch (error) {
      actions.setError(error.message);
      actions.setLogs([]);
    }
  }),
  addPost: action((state, payload) => {
    // state.posts = [...state.posts, payload];
    state.posts = [...state.posts, payload];
  }),
  deletePost: action((state, id) => {
    const newPost = state.posts.filter((post) => post.id !== id);
    state.posts = newPost;
  }),

  performPostAdd: thunk(async (actions, payload) => {
    action.loading = true;
    actions.addPost(payload);
    axios
      .post(`${adminPost}`, payload)
      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.error(err);
        // action.setError(err);
      });
  }),
  addComment: action((state, newComment) => {
    state.post.comments = [...state.post.comments, newComment];
  }),
  perfromCommentAdd: thunk((actions, payload) => {
    action.loading = true;
    axios
      .post(`${comment}/${payload.id}`, payload.commentObject)
      .then((res) => {
        console.log(res.data);
        const newComment = res.data;
        actions.addComment(newComment);
      })
      .catch((err) => {
        console.error(err);
      });
  }),
  // selectedComment = [selectedComment.replies, ...newReply];),

  addReply: action((state, payload) => {
     state.test = state.post.comments[payload.cid].replies.push(payload.newReply)

    // selectedComment = [selectedComment.replies, ...payload.newReply];
  }),

  performReplyAdd: thunk((actions, payload) => {
    axios
      .post(`${reply}/${payload.cid}`, payload.replyObject)
      .then((res) => {
        console.log(res);
        const newReply = res.data;
        const cid = payload.cid;
       const  plod = {
          newReply,
          cid,
        };
        actions.addReply(plod);
        console.log({ cid, newReply });
      })
      .catch((error) => {
        console.error(error);
        actions.setError(error.message);
      });
  }),

  performPostDelete: thunk((actions, id) => {
    action.loading = true;
    axios
      .delete(`${adminPost}/${id}`)
      .then((res) => {
        console.log(res);
        actions.deletePost(id);
      })
      .catch((error) => {
        console.error(error);
        actions.setError(error.message);
      });
  }),
};
export default voxStore;
