import React, { Component } from "react";
import { Layout, Menu, Switch } from "antd";
import BallotTwoToneIcon from "@material-ui/icons/BallotTwoTone";
import CategoryTwoToneIcon from "@material-ui/icons/CategoryTwoTone";
import PeopleAltTwoToneIcon from "@material-ui/icons/PeopleAltTwoTone";
import DashboardTwoToneIcon from "@material-ui/icons/DashboardTwoTone";
import TrafficTwoToneIcon from "@material-ui/icons/TrafficTwoTone";
import BuildTwoToneIcon from "@material-ui/icons/BuildTwoTone";
import WbSunnyTwoToneIcon from "@material-ui/icons/WbSunnyTwoTone";
import Dashboard from "./Dashboard";
import Posts from "./Posts";
import Category from "./Category";
import Subscriber from "./Subscriber";
import VisitLogs from "./VisitLogs";
import OperationLogs from "./OperationLogs";
import Brightness2TwoToneIcon from "@material-ui/icons/Brightness2TwoTone";
const { Header, Content, Footer, Sider } = Layout;
class Admin extends Component {
  state = {
    collapsed: true,
    component: "1",
    theme: "light",
    classval: "light",
  };
  onCollapse = (collapsed) => {
    console.log(collapsed);
    this.setState({ collapsed });
  };
  changeTheme = (value) => {
    this.setState({
      theme: value ? "dark" : "light",
      classval: value ? "dark" : "light",
    });
  };
  renderComp = (props) => {
    this.setState({ component: props.key });
  };
  render() {
    const { collapsed, component } = this.state;
    const algorithm = () => {
      if (component === "1") {
        return <Dashboard theme />;
      } else if (component === "Posts") {
        return <Posts />;
      } else if (component === "Category") {
        return <Category />;
      } else if (component === "Subscriber") {
        return <Subscriber />;
      } else if (component === "VisitLogs") {
        return <VisitLogs />;
      } else if (component === "OperationLogs") {
        return <OperationLogs />;
      }
    };
    const dyanmic = algorithm();
    const { classval } = this.state;
    return (
      <Layout style={{ minHeight: "100vh" }}>
        <Sider
          theme={this.state.theme}
          collapsible
          collapsed={collapsed}
          onCollapse={this.onCollapse}
        >
          <div className="logo" />
          <Menu
            theme={this.state.theme}
            defaultSelectedKeys={["1"]}
            mode="inline"
            style={{ minHeight: "100vh" }}
          >
            <Menu.Item
              onClick={this.renderComp}
              active
              key="1"
              icon={<DashboardTwoToneIcon />}
            >
              <span style={{ marginLeft: "25px" }}>Dashboard</span>
            </Menu.Item>
            <Menu.Item
              onClick={this.renderComp}
              key="Posts"
              id="d"
              icon={<BallotTwoToneIcon />}
            >
              <span style={{ marginLeft: "25px" }}>Posts</span>
            </Menu.Item>
            <Menu.Item
              onClick={this.renderComp}
              key="Category"
              icon={<CategoryTwoToneIcon />}
            >
              <span style={{ marginLeft: "25px" }}>Category</span>
            </Menu.Item>
            <Menu.Item
              onClick={this.renderComp}
              key="Subscriber"
              icon={<PeopleAltTwoToneIcon />}
            >
              <span style={{ marginLeft: "25px" }}>Subscribers</span>
            </Menu.Item>
            <Menu.Item
              onClick={this.renderComp}
              key="VisitLogs"
              title="Site Traffic"
              icon={<TrafficTwoToneIcon />}
            >
              <span style={{ marginLeft: "25px" }}>Site Traffice</span>
            </Menu.Item>
            <Menu.Item
              onClick={this.renderComp}
              key="OperationLogs"
              icon={<BuildTwoToneIcon />}
            >
              <span style={{ marginLeft: "25px" }}>Activity Logs</span>
            </Menu.Item>
          </Menu>
        </Sider>
        <Layout className="site-layout">
          {/* <Header className="site-layout-background">
            <ul className="header">
              <li>Telugu Planet Official</li>
              <li className="switch">
                <Switch
                  disabled={true}
                  name="toggle-dark-mode"
                  checkedChildren={<WbSunnyTwoToneIcon fontSize="small" />}
                  unCheckedChildren={
                    <Brightness2TwoToneIcon fontSize="small" />
                  }
                  onChange={this.changeTheme}
                />
              </li>
            </ul>
          </Header> */}
          <Content style={{ margin: "0px" }}>
            <div
              className="site-layout-background"
              style={{ padding: 0, minHeight: "82vh" }}
            >
              <div
                style={{ minHeight: "100vh", padding: "20px" }}
                className={classval}
              >
                {dyanmic}
              </div>
            </div>
          </Content>
          <Footer style={{ textAlign: "center" }}>
            Copyright 2020 Telugu Planet Official All rights reserved
          </Footer>
        </Layout>
      </Layout>
    );
  }
}

export default Admin;
